import { useEffect, useState } from "react";

const NF = () => {
  const [s, ss] = useState(true);
  const closet = (e) => {
    const ids = e.target.closest("#fId");
    const ids1 = e.target.closest("#fId1");
    console.log(ids, "hiiiii", ids1);
    if (ids) {
      ss(false);
    }
    if (ids1) {
      ss(true);
    }
  };
  useEffect(() => {
    window.addEventListener("click", (e) => closet(e));
    return () => window.addEventListener("click", (e) => closet(e));
  }, []);
  return (
    <div id="fId1" style={{ border: "1px solid red", width: "7rem" }}>
      <h1>Outer Form</h1>
      {s ? (
        <h1 id="fId" style={{ border: "1px solid blue", width: "7rem" }}>
          Inner Form
        </h1>
      ) : (
        ""
      )}
    </div>
  );
};
export default NF;
